// Terminal-optimized monospace font stack
// Using system fonts first for better performance and consistency

@font-face {
  font-family: 'Cascadia Code';
  font-style: normal;
  font-weight: 300;
  src: local('Cascadia Code Light'), local('CascadiaCode-Light'), url('#{$fontsPath}CascadiaCode-Light.woff2') format('woff2'), url('#{$fontsPath}CascadiaCode-Light.woff') format('woff');
  font-display: swap; }

@font-face {
  font-family: 'Cascadia Code';
  font-style: normal;
  font-weight: 400;
  src: local('Cascadia Code Regular'), local('CascadiaCode-Regular'), url('#{$fontsPath}CascadiaCode-Regular.woff2') format('woff2'), url('#{$fontsPath}CascadiaCode-Regular.woff') format('woff');
  font-display: swap; }

